.candidates {
    display: flex !important;
    align-items: center;
    justify-content: space-between !important;
    padding: 20px;
    border-bottom: 1px solid #eee;
}

.candbio {
    display: flex;
    flex:0.8;
    font-weight:600;
    font-size: 13px;
    color: var(--uccaccent-dark);
    text-transform: uppercase;
}

.candbio img {
    height: 100px;
    margin-right:15px;
}

.votecover {
   display: flex;
   flex: 0.2;
}

.votecover img {
    height: 80px;
    margin-right: 15px;
    
}

.votecover button {
   background:var(--uccaccent-light);
   border:3px solid var(--uccmain);
   border-radius: 5px;
   color: var(--uccmain);
   font-size:15px;
   font-weight: bolder;
   cursor: pointer;
}

.portfolio_cover {
  display: flex;
  flex-direction: column;
}

.portfolio {
  width: 100%;
  border-bottom: 1px dashed var(--uccmain);
  padding: 10px 25px;
  margin:10px auto;
  box-shadow: 0 0 2px #ddd inset;
  background: #f5f6f6;
  transition:opacity 0.8s ease-in-out;
}

.portfolio_title {
  text-align: center !important;
  display: flex;
  align-items: center;
  font-size: 1.5em;
  line-height: 1.4em;
  color: var(--uccaccent-dark)
}

.vote-btn {
    font-size: 25px;
    font-weight:bolder;
    color: var(--uccmain) !important;
}



.paginator {
    padding: 15px 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.paginator > span {
    padding: 4px 20px;
    background: var(--uccmain);
    box-shadow: 0 0 2px var(--uccmain);
    border-radius: 15px;
    font-size: 13px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    user-select: none;
}

.pageview {
    flex: 0.7;
    display:flex;
    align-items: center;
    justify-content: center
}

.pagehide {
    visibility: hidden;
}

.page_indicator {
    height:20px;
    width: 20px;
    margin:5px;
    border-radius: 50%;
    border:3px solid #ddd;
    background: #f5f5f5;
    box-shadow: 0 1px 3px #666;
    cursor:pointer;
}

.complete { background: #02c242; }
.complete_active { background:var(--uccmain) ; border-color:#02c242; }
.current { background: var(--uccmain); }

