.layout-login__reset {
    z-index: 1;
    box-sizing: border-box;
    width: 431px;
    position: absolute;
    top: -65px;
    left: 35%;
    bottom: 10px;
    padding-bottom: 30px;
    border-left: 3px solid rgb(19 61 113);
    border-right: 3px solid rgb(19 61 113);
    border-top: 3px solid rgb(19 61 113);
    background: #0b1f5a;
    border-radius: 10px;
    overflow: hidden;
}

.layout-login__inner input[type=text], .layout-login__inner input[type=email], .layout-login__inner input[type=password], .layout-login__inner input[type=date], .layout-login__inner input::placeholder {
    padding: 0;
    border-radius: 0px;
    border: none;
    outline: none;
    border-bottom: 1px solid rgba(161,186,216,.3);
    background: transparent;
    font-size: 18px;
    line-height: 44px;
    height: 45px;
    box-sizing: border-box;
    color: rgb(161 186 216);
    transition: color .1s linear, border-color .1s linear;
}


.login-form__title {
    font-size: 28px;
}

@media (max-width: 576px) {
    .layout-login__reset {
        left: 0;
        width:auto; 
    }
}

