.pseudo {
  cursor: pointer;
}
.label {
  display: inline-block;
  box-sizing: border-box;
  padding: 2px 4px;
  vertical-align: top;
  color: #333333;
  font: 10px/13px 'RobotoBold', Arial, sans-serif;
  font-weight: normal;
  background: #ababab;
  border-radius: 2px;
  cursor: default;
}
.label_red {
  background: #ff6c6c;
}
.label_yellow {
  background: #dede6c;
}
.layout {
  height: 100%;
  position: relative;
}
.layout:before {
  content: '';
  height: 70px;
  position: absolute;
  top: 0px;
  right: 0px;
  left: 0px;
  background: #333333;
}
.layout_dark {
  background: #333333;
}
.layout__logo {
  z-index: 2;
  width: 450px;
  height: 91px;
  position: absolute;
  top: 20px;
  left: 22px;
  background: url(/static/media/logo_portal.2a242f62.png) no-repeat;
  background-size: fit;
}
.layout__content {
  z-index: 1;
  box-sizing: border-box;
  height: 100%;
  position: relative;
  padding-top: 70px;
  overflow: hidden;
}
.layout__inner {
  min-height: 100%;
  position: relative;
}
.layout__inner_columns {
  height: 100%;
}
.layout__columns {
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: stretch;
}
.layout__column {
  height: 100%;
  position: relative;
  box-sizing: border-box;
}
.layout__column_left .layout__column__content {
  width: 250px;
  position: static;
}
.layout__column_scroll .layout__column__content {
  overflow-y: auto!important;
}
.layout__column_mid {
  width: 100%;
  height: 100%;
  background: #fff;
}
.layout__column_mid .layout__column__content {
  background: #f8f8f8;
  overflow-y: scroll;
}
.layout__column__content {
  box-sizing: border-box;
  height: 100%;
  position: relative;
}
.layout__column__title {
  margin: 0px;
  font: 18px 'RobotoMedium', Arial, sans-serif;
  font-weight: normal;
}
@-webkit-keyframes layout-preloader {
  0% {
    background-position: -80px;
  }
  100% {
    background-position: 80px;
  }
}
@keyframes layout-preloader {
  0% {
    background-position: -80px;
  }
  100% {
    background-position: 80px;
  }
}
.layout-preloader {
  height: 100%;
  position: relative;
}
.layout-preloader__icon {
  width: 54px;
  height: 62px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: calc(-31px) 0px 0px calc(-27px);
  /*background: url('../img/icon/icon_layout-preloader.svg') no-repeat;
  background-size: cover;*/
  overflow: hidden;
}
.layout-preloader__icon:before {
  content: '';
  position: absolute;
  top: -62px;
  right: -54px;
  bottom: -62px;
  left: -54px;
  background: linear-gradient(30deg, #e8eaec 0%, #e8eaec 45%, rgba(245, 245, 245, 0) 50%, #e8eaec 55%, #e8eaec 100%);
  background-position: -80px;
  opacity: .5;
  -webkit-animation: layout-preloader 2s 1s 10;
          animation: layout-preloader 2s 1s 10;
}
.content {
  height: 100%;
  box-sizing: border-box;
}
.footer {
  font-size: 10px;
  font-weight: bolder;
  color: #787d82;
  padding: 15px 20px;
  text-align: center;
  position: absolute;
  bottom: 0px;
  width: 100%;
  box-sizing: border-box;
}
.footer_short {
  bottom: 10px;
  left: 10px;
  width: 220px;
  padding: 0px;
  z-index: 11;
}
.footer__inner {
  display: inline-block;
}
/* Login layout */
.layout-login {
  height: 100vh;
  margin-top: -70px;
  padding-top: 70px;
  background: #0b1f5a;
  color: #fff;
}

.layout-login__inner {
  z-index: 1;
  box-sizing: border-box;
  max-width: 1600px;
  height: 100%;
  position: relative;
  padding: 0px calc(471px) 0px 40px;
  background: #0b1f5a;
}

.layout-login__inner p {
  color: #fff;
}

.layout-login__inner input[type=text],
.layout-login__inner input[type=email],
.layout-login__inner input[type=password] {
  padding: 0;
  border-radius: 0px;
