  font-weight: 500;
  color: rgb(117, 134, 152);
  position: relative;
}

.nav-tabs-line .nav-link::after {
  position: absolute;
  left: 0px;
  bottom: -2px;
  content: "";
  width: 0px;
  height: 4px;
  background: rgb(154, 76, 101);
  transition: all 0.4s ease 0s;
}

.nav-tabs-line .nav-link.active {
