}

.nav-tabs-icon img {
  transition: all 0.4s ease 0s;
  width: 100%;
}

.nav-tabs-icon img:first-of-type {
  opacity: 1;
}

.nav-tabs-icon img:last-of-type {
  position: absolute;
  top: 0px;
  left: 0px;
  opacity: 0;
}

.active>.nav-tabs-icon img:first-of-type {
  opacity: 0;
