.accordion-heading {
  cursor: pointer;
  color: rgb(37, 57, 146);
  font-weight: 500;
  transition: all 0.4s ease 0s;
  margin-bottom: 8px;
  padding-left: 20px;
  font-size: 1em;
  position: relative;
}

.accordion-heading::before,
.accordion-heading::after {
  position: absolute;
  content: "";
}

.accordion-heading::before {
  left: 0px;
  top: 10px;
  height: 1px;
  width: 11px;
  background: rgb(117, 134, 152);
}

.accordion-heading::after {
  left: 5px;
  top: 5px;
  height: 11px;
  width: 1px;
  background: rgb(117, 134, 152);
  -webkit-transform: scaleX(1) scaleY(0);
          transform: scaleX(1) scaleY(0);
  transition: all 0.3s ease 0s;
}

.accordion-heading.collapsed {
  color: rgb(117, 134, 152);
}

.accordion-heading.collapsed::after {
  -webkit-transform: scaleX(1) scaleY(1);
          transform: scaleX(1) scaleY(1);
}

.accordion-heading:hover {
  color: rgb(37, 57, 146);
}

.accordion-content {
  padding: 0px 0px 15px 20px;
}

.accordion-s2 .accordion-heading {
  padding-left: 0px;
  padding-right: 20px;
}

.accordion-s2 .accordion-heading::before {
  left: auto;
  right: 0px;
}

.accordion-s2 .accordion-heading::after {
  left: auto;
  right: 5px;
}

.accordion-s2 .accordion-content {
  padding: 0px 20px 15px 0px;
}

