.timeline {
  position: relative;
  padding: 15px 0px;
}

.timeline-wrap {
  position: relative;
  overflow: hidden;
  height: 100%;
  min-height: 300px;
}

.timeline-wrap .timeline-innr {
  overflow-x: hidden;
  height: 100%;
  position: absolute;
  padding-right: 20px;
  padding-bottom: 30px;
}

.timeline-wrap.loaded .timeline-innr {
  padding-bottom: 0px;
}

.timeline-load {
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  text-align: center;
  padding: 40px 0px 0px;
  background: linear-gradient(rgba(255, 255, 255, 0) 0%, rgb(255, 255, 255) 65%, rgb(255, 255, 255) 100%);
}

.timeline-load .link {
  display: block;
  text-transform: uppercase;
  font-weight: 400;
  color: rgb(117, 134, 152);
}

.timeline-load .link:hover {
  color: rgb(44, 128, 255);
}

.timeline-line {
  position: absolute;
  height: 100%;
  width: 2px;
  border-radius: 2px;
  background: rgb(227, 234, 241);
  top: 0px;
  left: 90px;
}

.timeline-item {
  display: flex;
}

.timeline-item.hidden {
  display: none;
}

.timeline-time {
  width: 90px;
  flex-shrink: 0;
  font-weight: 500;
  margin: 10px 0px;
  position: relative;
  align-self: flex-start;
}

.timeline-time::after {
  position: absolute;
  height: 14px;
  width: 14px;
  border-radius: 50%;
  border: 2px solid rgb(255, 255, 255);
  background: rgb(44, 128, 255);
  content: "";
  top: 5px;
  right: -8px;
}

.light>.timeline-time::after {
  background: rgb(255, 255, 255);
  border-color: rgb(227, 234, 241);
}

.secondary>.timeline-time::after {
  background: rgb(37, 57, 146);
  border-color: rgb(255, 255, 255);
}

.success>.timeline-time::after {
  background: rgb(0, 210, 133);
  border-color: rgb(255, 255, 255);
}

.warning>.timeline-time::after {
  background: rgb(255, 193, 0);
  border-color: rgb(255, 255, 255);
}

.danger>.timeline-time::after {
  background: rgb(255, 104, 104);
  border-color: rgb(255, 255, 255);
}

.timeline-time span {
  display: block;
  font-size: 12px;
  line-height: 1;
  color: rgb(117, 134, 152);
  font-weight: 400;
}

.timeline-content {
  flex-grow: 1;
  margin: 10px 0px 10px 25px;
}

.timeline-content p,
.timeline-content-title {
  margin-bottom: 0px;
}

.timeline-content-info {
  font-size: 12px;
  display: block;
  padding-top: 8px;
  color: rgb(117, 134, 152);
}

a+.timeline-content-info {
  padding-top: 2px;
}

p+.timeline-content-info {
  padding-top: 5px;
}

