    --uccmain:#263876;
    --uccmain-dark:#0a2f72;
    --uccmain-light:#e8f5fa;
    --uccsec:#ffee1f;
    --uccsec-dark:#e5d40c;
    --uccaccent:#d5242c;
    --uccaccent-dark:#a22d43;
}
